.landingPagePromo {
  min-height: 200px;
  height: auto;
  .hvrbox {
    position: relative;
    margin-top: 50px;
    width: 100%;
    height: 100%;
  }

  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0);
    transition: background 0.5s ease;
  }

  .hvrbox:hover .overlay {
    display: block;
    background: rgba(0, 0, 0, .3);
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 300px;
  }

  .title {
    position: absolute;
    width: 100%;
    left: 0;
    top: 40%;
    font-weight: 700;
    font-size: 30px;
    text-align: center;
    color: white;
    z-index: 1;
    transition: top .5s ease;
  }

  .hvrbox:hover .title {
    top: 30%;
  }

  .button {
    position: absolute;
    width: 100%;
    left:0;
    top: 60%;
    text-align: center;
    opacity: 0;
    transition: opacity .35s ease;
  }

  .button a {
    width: 40%;
    padding: 12px 48px;
    text-align: center;
    color: white;
    border: solid 2px white;
    z-index: 1;
  }

  .hvrbox:hover .button {
    opacity: 1;
  }
}

.newsContainer {
  min-height: 300px;
  height: auto;
  margin: 10px;
  img {
    width: 100%;
    height: 300px;
    object-fit: contain;
  }
  .content {
    margin: 20px;

    .linkButton{
      text-decoration:none;
    }

    .linkButton:hover{
      color:#fff;
    }
  }
}

.newsContainerTable {
  min-height: 300px;
  height: auto;
  img {
    width: 100%;
    height: 300px;
    object-fit: contain;
  }
  .content {
    margin: 20px;
  }

  h4{
    overflow-wrap:break-word;
  }

  .newsContainerItem {
    min-height: 200px;
    height: auto;
    margin: 10px;
    img {
      width: 100%;
      height: 200px;
      object-fit: contain;
    }
    .content {
      word-wrap: break-word;
      margin: 20px;
      .dateNews {
        font-size: 10px;
      }
      .contentNews {
        font-size: 12px;
      }
    }
  }
}

  .nearestOutlet {
    width: 100%;
    height: 400px;
    position: relative;

    .outletData {
      .nav-item {
        width: 25%;
        text-align: center;
        padding: 0px;
      }
      .nav-link{
        padding: 20px;
        font-size: 14px;
      }
      .nav-link.active {
        background-color: transparent;
        font-weight: bold;
      }
    }
  }
