@import "../../../../2App/baseTheme.scss";

.promoList {
  height: auto;
  margin-top: -25%;

  .promoSection {
    padding-left:0px;
    padding-right:0px;
  }
  .promoBanner {
    img {
      background: black;
      object-fit: contain;
      width: 100%;
      border-radius: 15px;
      height: 400px;
    }

    .content {
      .contentTitle {
        margin-top: 3%;
        word-wrap: break-word;
      }

      .contentDesc {
        margin-top: 2%;
        word-wrap: break-word;
      }
    }
  }

  .promoCard {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 15px;
    margin-bottom: 5%;
  }

  img {
    object-fit: contain;
    width: 100%;
    border-radius: 15px;
  }

  .title {
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 10%;
    font-weight: 700;
    font-size: 20px;
    text-align: center;
    color: white;
    z-index: 1;
    transition: top .5s ease;
  }

  .hvrbox:hover .title {
    top: 30%;
  }

  .button {
    position: absolute;
    width: 100%;
    left:0;
    top: 60%;
    text-align: center;
    opacity: 0;
    transition: opacity .35s ease;
  }

  .button a {
    width: 40%;
    padding: 12px 48px;
    text-align: center;
    color: white;
    border: solid 2px white;
    z-index: 1;
  }

  .hvrbox:hover .button {
    opacity: 1;
  }
}

.promoDetail{
  .headerBG{
    padding-bottom: 20%;
    .promoTitle{
      font-weight: 700;
    }
  }
  .container-detail-promo{
    margin-top: -15%;
    .banner-detail-promo{
      background-color: black;
      border-radius: 10px;
    }
    .content-detail-promo{
      margin-left: 0;
      margin-right: 0;
      padding-left: 0;
      padding-right: 0;
      word-wrap: break-word;
    }
    .available-promo{
      text-align: right;
      .available-promo-title{
        font-weight: 600;
        font-size: 20px;
      }
    }
  }
}
