@import "../../../../2App/baseTheme.scss";

.outletList{

  background:$color-backgroundColor;

  .linkButton{
    text-decoration:none;
  }

  .linkButton:hover{
    color:#fff;
  }


  .image{
    width:100%;
  }
}



.outletDetail{

  .headerBG{
    padding-bottom: 18%;
    margin-top:-5%;
  }

  .outletName{
    font-weight: 700;
  }

  background:$color-backgroundColor;

  .promoInOutlet {

    .hvrbox {
      position: relative;
      width: 100%;
      height: 100%;
      margin-bottom: 5%;
    }

    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      margin-bottom: 5%;
      width: 100%;
      height: 93%;
      background: rgba(0, 0, 0, 0);
      transition: background 0.5s ease;
      border-radius: 10px;
    }

    .hvrbox:hover .overlay {
      display: block;
      background: rgba(0, 0, 0, .3);
    }

    img {
      object-fit: contain;
      width: 100%;
      border-radius: 10px;
    }

    .title {
      position: absolute;
      width: 100%;
      left: 0;
      bottom: 5%;
      font-weight: 700;
      font-size: 15px;
      text-align: center;
      color: white;
      z-index: 1;
      transition: top .5s ease;
    }

    .button {
      position: absolute;
      width: 100%;
      top: 40%;
      text-align: center;
      opacity: 0;
      transition: opacity .35s ease;
    }

    .button a {
      width: 40%;
      padding: 3px 6px;
      text-align: center;
      color: white;
      border: solid 2px white;
      z-index: 1;
    }

    .hvrbox:hover .button {
      opacity: 1;
    }
  }

  .roomInOutlet {

    .roomCard {
      position: relative;
      width: 100%;
      height: 100%;
      margin-bottom: 15%;
    }

    img {
      object-fit: cover;
      width: 100%;
      height:300px;
      border-radius: 10px 10px 0 0;
    }

    .title {
      position: absolute;
      width: 100%;
      left: 0;
      bottom: 0;
      padding-top: 10px;
      padding-bottom: 10px;
      margin-bottom: 30%;
      background-color: black;
      font-weight: 700;
      font-size: 15px;
      text-align: center;
      color: white;
      z-index: 1;
      transition: top .5s ease;
    }

    .buttonRoom {
      margin-top: 10%;
      position: relative;
      width: 100%;
      text-align: center;
    }

    .buttonRoom a {
      width: 40%;
      padding: 10px 50px;
      text-align: center;
      font-size: 12px;
      color: white;
      background-color: black;
      border-radius: 5px;
      border: solid 2px black;
      z-index: 1;
    }
  }

  .banner-detail-outlet{
    margin-bottom: 5%;
  }

  .container-detail-outlet{
    margin-top: -15%;
  }

  a{
    text-decoration:none;
    color:#000;
  }


  .image{
    width:100%;
  }
}

.roomDetail{
  .headerBG.top-title{
    padding-bottom: 18%;
    margin-top:-5%;
  }

  .roomName{
    font-weight: 700;
  }

  .banner-detail-room{
    margin-left: 15px;
    margin-right: 15px;
    border-radius: 10px;
  }

  .container-detail-room{
    margin-top: -15%;
    .other-picture {
      margin-top: 2%;
      padding-right: 0;
      padding-left: 0;
      margin-right: 0;
      margin-left: 0;
      .img-room {
        width: 100%;
        height: 100px;
        border-radius: 10px;
      }
    }

    .content-room{
      margin-top: 5%;
      margin-left: 0;
      margin-right: 0;
      .title-desc{
        font-weight: 500;
        padding-left: 0;
        padding-right: 0;
      }
      .content-desc{
        padding-left: 0;
        padding-right: 0;
      }
    }
    .buttonBookRoom {
      margin-bottom: 10%;
      position: relative;
      width: 100%;
    }

    .buttonBookRoom a {
      width: 40%;
      padding: 10px 50px;
      text-align: center;
      font-size: 12px;
      color: white;
      background-color: deeppink;
      border-radius: 5px;
      border: solid 2px deeppink;
      z-index: 1;
    }
  }

  .roomCard {
    position: relative;
    width: 100%;
    height: 100%;
    margin-bottom: 15%;
  }

  img {
    object-fit: cover;
    width: 100%;
    height:200px;
    border-radius: 10px 10px 0 0;
  }

  .title {
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: black;
    font-weight: 700;
    font-size: 15px;
    text-align: center;
    color: white;
    z-index: 1;
    transition: top .5s ease;
  }

  #other-room{
    padding:5% 2%;
  }

  .other-room-title{
    margin-bottom: 3%;
  }
}
