@import "baseTheme";



H1{
  font: 24px Raleway !important;
  font-weight: 800 !important;
  color: #000000 !important;
}

.headerBG{
  background: $color-headerBackground;

  // Small devices (landscape phones, 576px and up)
  @media (min-width: 576px) {
