  // Medium devices (tablets, 768px and up)
  @media (min-width: 768px) {
  }

  // Large devices (desktops, 992px and up)
  @media (min-width: 992px) {
  }

  // Extra large devices (large desktops, 1200px and up)
  @media (min-width: 1200px) {
  }
}

.mainBG{
  background: $color-mainBackground;
}

.nav-drawer{
  background: $color-headerBackground;
}



.side-content-left{
  background: $color-headerBackground;
  height: 50vh;

  // Medium devices (tablets, 768px and up)
  @media (min-width: 768px) {
    height: 100vh;
  }
}

.side-content-top{
  margin-right:0px;
  margin-left:0px;
  margin-top:0px;

  // Large devices (desktops, 992px and up)
  @media (min-width: 992px) {
    margin-top:60px;
  }

}

.side_btn{
  margin: 0px;
  padding: 20px !important;
  text-align: center;
  vertical-align: middle;
  background: $color-accentColor;
  color:$color-onPrimary;
  position: absolute;
  right: 0px;
}

.side-nav{
  width:80% !important;
}
.side-nav.fixed{
  background: $color-headerBackground;

  .login-sidenav{
    background: #ffffff;
    li{
      border-bottom:1px solid #ccc;
    }
    a{
      color:#000000;
      font-weight: bold;
    }
  }

  .logout-sidenav{
    background: $color-accentColor;

    a{
      color:#ffffff;
      font-weight:bold;
      text-align:center;
      padding-left:0px;
    }
  }

  .profile-sidenav{
    padding: 20px;
    background: #ffffff;
    color:#000;

    a{
      color:#000;
      font-size:0.8em;
      font-weight: bold;

      height: 36px;
      line-height: 36px;
    }
  }

  .sidenav-menus{
    background: $color-headerBackground;

    li{
      border-top: 1px solid #2c4660;
    }

    a{
      color:#ffffff;
      font-weight: bold;
    }
  }
}

.imgContainer{
  float:left;
  width:100px;
  height:100px;
  border-radius: 50%;
  margin-right:10px;
}

.profileData{
  padding:10px;
}


.lineIndicatorBlue{
  float:left;
  width:20px;
  padding:5px;
  background: $color-indicatorBlue;
  vertical-align:middle;
  margin-right:20px;
}

