header {
  padding: 156px 0 100px;
}

section {
  padding: 150px 0;
}

.navbar{
  background:$color-primary;
  padding:0px;
}

.navbar .navbar-brand{
  padding:20px;
  margin-left:20px;
}

.navbar.navbar-dark .breadcrumb .nav-item .nav-link:hover, .navbar.navbar-dark .navbar-nav .nav-item .nav-link{
  color: rgba(255,255,255,.85);
  font-size:0.9em;
  -webkit-transition: .35s;
  -o-transition: .35s;
  transition: .35s;
}

.navbar.navbar-dark .breadcrumb .nav-item .nav-link:hover, .navbar.navbar-dark .navbar-nav .nav-item .nav-link:hover{
  color: rgba(255,255,255, 1);

}

.nav-item{
  padding:20px;
}

.nav-login-account{
  background:#fafafa;
  border-left:1px solid #ccc;

  a:hover{
    color:#333333;
  }

  .nav-link{
    font-weight: bold;
  }
}

.navbar-dark .navbar-nav .nav-login-account .nav-link{
  color:#000000 !important;
}



.form-control::-webkit-input-placeholder { color: #ccc; }  /* WebKit, Blink, Edge */
.form-control:-moz-placeholder { color: #ccc; }  /* Mozilla Firefox 4 to 18 */
.form-control::-moz-placeholder { color: #ccc; }  /* Mozilla Firefox 19+ */
.form-control:-ms-input-placeholder { color: #ccc; }  /* Internet Explorer 10-11 */
.form-control::-ms-input-placeholder { color: #ccc; }  /* Microsoft Edge */



.react-datepicker-wrapper{
  width:100%;
}

.react-datepicker__input-container{
  width:100%;
}


.default_btn{
  width:100%;
  margin-top:0px;
  margin-left: 0px;
  margin-right: 0px;
  margin-bottom: 0px;
  height: auto;
  background-color: $color-primary !important;
  color: $color-onPrimary !important;;
  border-color: $color-primary !important;;
  &:hover{
    background-color: $color-primary !important;
  }
}



.dropdown-menu{
  border:0px;
  top: 80px;
  right: 100px;
  left: unset;
  width: 300px;
  box-shadow: 0px 5px 7px -1px #c1c1c1;
  padding-bottom: 0px;
  padding: 0px;
}
.dropdown-menu:before{
  content: "";
  position: absolute;
  top: -20px;
  right: 12px;
  border:10px solid #343A40;
  border-color: transparent transparent #343A40 transparent;
}













