@import "../../../../2App/baseTheme.scss";

.newsList{
  background:$color-backgroundColor;
  a{
    text-decoration:none;
    color:#000;
  }
}

.newsDetail{
  .headerBG{
    padding-bottom: 20%;
    .newsTitle{
      font-weight: 700;
    }
  }
  .container-detail-news{
    margin-left: 5%;
    margin-right: 5%;
    margin-top: -15%;
    .banner-detail-news{
      background-color: black;
      border-radius: 10px;
    }
    .content-detail-news{
      margin-left: 0;
      margin-right: 0;
      padding-left: 0;
      padding-right: 0;
    }
    .available-news{
      text-align: right;
      .available-news-title{
        font-weight: 600;
        font-size: 20px;
      }
    }
  }
}