.receiptList{
  margin:5% 0;
  .receiptItem{
    .outlet-name{
      float: right;
    }
  }
  a
  {
    color:black;
    text-decoration: none;
    background-color: none;
  }
}

#receiptListContainer{
  .BaseModal.flexibleWidth{
    text-align: left;
  }
  .BaseModal .innerModal{
    padding: 0 0 15px 0;
  }
  .ModalContent.container{
    padding-right:0px;
  }
}

.receiptDetail{
  .headerBG{
    padding: 10px;
  }
  .receipt-detail-content{
    text-align:right;
  }
  .result{
    background-color: lightgrey;
    padding: 2% 0;
  }
  div{
    font-size: 12px;
  }

  .receiptSection{
    .col-md-12.row .col-md-6{
      padding:0 0 0 15px;
    }
  }
}