H1 {
  font: 24px Raleway !important;
  font-weight: 800 !important;
  color: #000000 !important; }

.headerBG {
  background: #061625; }

.mainBG {
  background: #f1f1f1; }

.nav-drawer {
  background: #061625; }

.side-content-left {
  background: #061625;
  height: 50vh; }
  @media (min-width: 768px) {
    .side-content-left {
      height: 100vh; } }

.side-content-top {
  margin-right: 0px;
  margin-left: 0px;
  margin-top: 0px; }
  @media (min-width: 992px) {
    .side-content-top {
      margin-top: 60px; } }

.side_btn {
  margin: 0px;
  padding: 20px !important;
  text-align: center;
  vertical-align: middle;
  background: #e237c7;
  color: white;
  position: absolute;
  right: 0px; }

.side-nav {
  width: 80% !important; }

.side-nav.fixed {
  background: #061625; }
  .side-nav.fixed .login-sidenav {
    background: #ffffff; }
    .side-nav.fixed .login-sidenav li {
      border-bottom: 1px solid #ccc; }
    .side-nav.fixed .login-sidenav a {
      color: #000000;
      font-weight: bold; }
  .side-nav.fixed .logout-sidenav {
    background: #e237c7; }
    .side-nav.fixed .logout-sidenav a {
      color: #ffffff;
      font-weight: bold;
      text-align: center;
      padding-left: 0px; }
  .side-nav.fixed .profile-sidenav {
    padding: 20px;
    background: #ffffff;
    color: #000; }
    .side-nav.fixed .profile-sidenav a {
      color: #000;
      font-size: 0.8em;
      font-weight: bold;
      height: 36px;
      line-height: 36px; }
  .side-nav.fixed .sidenav-menus {
    background: #061625; }
    .side-nav.fixed .sidenav-menus li {
      border-top: 1px solid #2c4660; }
    .side-nav.fixed .sidenav-menus a {
      color: #ffffff;
      font-weight: bold; }

.imgContainer {
  float: left;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-right: 10px; }

.profileData {
  padding: 10px; }

.lineIndicatorBlue {
  float: left;
  width: 20px;
  padding: 5px;
  background: #377fe2;
  vertical-align: middle;
  margin-right: 20px; }

