header {
  padding: 156px 0 100px; }

section {
  padding: 150px 0; }

.navbar {
  background: #061625;
  padding: 0px; }

.navbar .navbar-brand {
  padding: 20px;
  margin-left: 20px; }

.navbar.navbar-dark .breadcrumb .nav-item .nav-link:hover, .navbar.navbar-dark .navbar-nav .nav-item .nav-link {
  color: rgba(255, 255, 255, 0.85);
  font-size: 0.9em;
  -webkit-transition: .35s;
  -o-transition: .35s;
  transition: .35s; }

.navbar.navbar-dark .breadcrumb .nav-item .nav-link:hover, .navbar.navbar-dark .navbar-nav .nav-item .nav-link:hover {
  color: white; }

.nav-item {
  padding: 20px; }

.nav-login-account {
  background: #fafafa;
  border-left: 1px solid #ccc; }
  .nav-login-account a:hover {
    color: #333333; }
  .nav-login-account .nav-link {
    font-weight: bold; }

.navbar-dark .navbar-nav .nav-login-account .nav-link {
  color: #000000 !important; }

.form-control::-webkit-input-placeholder {
  color: #ccc; }

/* WebKit, Blink, Edge */
.form-control:-moz-placeholder {
  color: #ccc; }

/* Mozilla Firefox 4 to 18 */
.form-control::-moz-placeholder {
  color: #ccc; }

/* Mozilla Firefox 19+ */
.form-control:-ms-input-placeholder {
  color: #ccc; }

/* Internet Explorer 10-11 */
