.default_btn {
  width: 100%;
  margin-top: 0px;
  margin-left: 0px;
  margin-right: 0px;
  margin-bottom: 0px;
  height: auto;
  background-color: #061625 !important;
  color: white !important;
  border-color: #061625 !important; }
  .default_btn:hover {
    background-color: #061625 !important; }

.dropdown-menu {
  border: 0px;
  top: 80px;
  right: 100px;
  left: unset;
  width: 300px;
  box-shadow: 0px 5px 7px -1px #c1c1c1;
  padding-bottom: 0px;
  padding: 0px; }

.dropdown-menu:before {
  content: "";
  position: absolute;
  top: -20px;
  right: 12px;
  border: 10px solid #343A40;
  border-color: transparent transparent #343A40 transparent; }
